import React from 'react';
import { Card, CardContent } from '@/components/ui/card';
import { Avatar, AvatarFallback, AvatarImage } from '@/components/ui/avatar';
import { Badge } from '@/components/ui/badge';

interface StudentCardProps {
  name: string;
  studentId: string;
  course: string;
  validUntil: string;
  profileImage: string;
  universityLogo: string;
  university: string;
}

export default function StudentCard({
  name,
  studentId,
  course,
  validUntil,
  profileImage,
  universityLogo,
  university
}: StudentCardProps) {
  return (
    <Card className="w-full max-w-sm mx-auto bg-gradient-to-br from-blue-50 to-indigo-100 border-2 border-blue-200 shadow-xl">
      <CardContent className="p-6">
        {/* Header com logo da universidade */}
        <div className="text-center mb-6">
          <img
            src={universityLogo}
            alt="Logotipo oficial da universidade em layout corporativo"
            className="h-16 w-auto mx-auto mb-2"
            onError={(e) => {
              e.currentTarget.src = "https://placehold.co/200x100?text=Universidade+Logo";
            }}
          />
          <h2 className="text-sm font-semibold text-blue-800 uppercase tracking-wide">
            {university}
          </h2>
          <p className="text-xs text-blue-600 font-medium">Carteirinha Digital</p>
        </div>

        {/* Foto do estudante */}
        <div className="flex justify-center mb-4">
          <Avatar className="w-24 h-24 border-4 border-white shadow-lg">
            <AvatarImage 
              src={profileImage} 
              alt="Foto de perfil do estudante em layout moderno"
              onError={(e) => {
                e.currentTarget.src = "https://placehold.co/150x150?text=Estudante+Profile+photo";
              }}
            />
            <AvatarFallback className="bg-blue-200 text-blue-800 text-xl font-bold">
              {name.split(' ').map(n => n[0]).join('').substring(0, 2)}
            </AvatarFallback>
          </Avatar>
        </div>

        {/* Informações do estudante */}
        <div className="space-y-3">
          <div className="text-center">
            <h3 className="text-lg font-bold text-gray-800 mb-1">{name}</h3>
            <Badge variant="secondary" className="bg-blue-100 text-blue-800 font-mono">
              {studentId}
            </Badge>
          </div>

          <div className="bg-white/70 rounded-lg p-3 space-y-2">
            <div>
              <p className="text-xs text-gray-600 uppercase tracking-wide font-medium">Curso</p>
              <p className="text-sm font-semibold text-gray-800">{course}</p>
            </div>
            
            <div>
              <p className="text-xs text-gray-600 uppercase tracking-wide font-medium">Válida até</p>
              <p className="text-sm font-semibold text-gray-800">{validUntil}</p>
            </div>
          </div>

          {/* Status ativo */}
          <div className="flex justify-center">
            <Badge className="bg-green-500 hover:bg-green-600 text-white">
              ✓ Ativa
            </Badge>
          </div>
        </div>

        {/* Código QR simulado */}
        <div className="mt-4 text-center">
          <div className="inline-block bg-white p-2 rounded border">
            <div className="w-16 h-16 bg-gray-800 rounded grid grid-cols-4 gap-px">
              {Array.from({ length: 16 }).map((_, i) => (
                <div 
                  key={i} 
                  className={`${Math.random() > 0.5 ? 'bg-white' : 'bg-gray-800'} rounded-sm`}
                />
              ))}
            </div>
          </div>
          <p className="text-xs text-gray-500 mt-1">Código de Verificação</p>
        </div>
      </CardContent>
    </Card>
  );
}
